<template>
  <div id="downGames">
   <div class="topNotice">选择下载</div>
   <divider>{{'点击下载APP' }}</divider>
   <grid>
     <grid-item link="/" >
       <img slot="icon" src="../../images/majiangicon.png" style="">
        <span slot="label">{{ '369互娱麻将' }}</span>
     </grid-item>
     <grid-item :link="{ path: '/'}">
       <img slot="icon" src="../../images/sanzhang_icon.png">
        <span slot="label">{{ '369互娱三张' }}</span>
     </grid-item>
     <grid-item link="/">
       <img slot="icon" src="../../images/majiangicon.png">
       <span slot="label">{{ '369互娱斗地主' }}</span>
     </grid-item>
   </grid>
     <tabbar class="vux-demo-tabbar" icon-class="vux-center">
       <tabbar-item>
         <span slot="label"><span class="vux-demo-tabbar-component">{{'敬请期待更多精品游戏'}}</span></span>
       </tabbar-item>
     </tabbar>
  </div>
</template>

<script>
import { Grid, GridItem, Divider, Tabbar, TabbarItem } from 'vux'

export default {

  components: {
    Grid,
    GridItem,
    Divider,
    Tabbar,
    TabbarItem
  },
  data () {
    return {

    }
  },
}
</script>

<style>
/* @import '~vux/src/styles/1px.less'; */

.divider, .topNotice{
  background-color: #377299;
  color: #fff;
}

.topNotice{
  text-align:center;
  color:#fff;
  padding-top:10px;
  font-family: Microsoft YaHei;
  font-size:28px;
}
.grid-center {
  display: block;
  text-align: center;
  color: #666;
}
#downGames .vux-divider{
  background-color: #377299;
  color:#f5f8fc;
  height:16px;
}
#downGames .vux-divider:after, #downGames .vux-divider:before{
  top:0
}

#downGames .weui-grid:before{
  border:none
}

.weui-grids .weui-grid__icon{
  width:88px;
  height:88px
}
.weui-grid span{
  color: #686868;
}
#downGames .vux-tabbar-simple{
  background-color:#223e51;
}
#downGames .weui-tabbar__label{
  color: #67c1f5;
}
</style>



